import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";

const d = [
  {
    id: "cuckoo20",
    last_login: "2024-04-19T11:52:49.149375Z",
    is_superuser: true,
    username: "admin",
    first_name: "",
    last_name: "",
    email: "gaeun9566@ewhain.net",
    is_staff: true,
    is_active: true,
    date_joined: "2024-04-19T11:52:30.234401Z",
    guild_id: 1, //   guild_id 
    user_birth: "2024-04-19",
    user_phone: "010-1234-1234",
    user_gender: "F",
    guild_admin: true,
  },
];

const Wargame = () => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [searchTerm, setSearchTerm] = useState("");
  const [levelFilter, setLevelFilter] = useState("all");
  const [typeFilter, setTypeFilter] = useState("all");
  const [currentPage, setCurrentPage] = useState(1);

  const navigate = useNavigate();

  const user = d[0];

  useEffect(() => {
    const fetchData = async () => {
      try {
        // const token = sessionStorage.getItem("token"); //    
        const response = await axios.get(
          `http://ec2-3-36-34-43.ap-northeast-2.compute.amazonaws.com:8000/guild/${user.guild_id}/wargame-list`,
          {
            // headers: {
            //   Authorization: `Bearer ${token}` //   
            // }
          }
        );
        setData(response.data);
      } catch (err) {
        setError(err);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, [user.guild_id]); // user.guild_id   

  const handleClick = (id) => {
    navigate(`/guild/${id}`);
  };

  const postsPerPage = 5;

  const filteredData = data.guild_wargame_list
    ? data.guild_wargame_list.filter((item) => {
        return (
          item.quiz_title.toLowerCase().includes(searchTerm.toLowerCase()) &&
          (levelFilter === "all" || item.quiz_level === levelFilter) &&
          (typeFilter === "all" || item.quiz_type === typeFilter)
        );
      })
    : [];

  const handleChangePage = (page) => {
    setCurrentPage(page);
  };

  const indexOfLastPost = currentPage * postsPerPage;
  const indexOfFirstPost = indexOfLastPost - postsPerPage;
  const currentPosts = filteredData.slice(indexOfFirstPost, indexOfLastPost);

  if (loading) {
    return <div>Loading...</div>;
  }

  if (error) {
    return <div>Error: {error.message}</div>;
  }

  return (
    <div className="guild_wargame_wrapper">
      <span className="wargame_title"></span>
      <div className="wargame_page_container">
        <div className="wargame-container">
          <div className="search-container">
            <input
              className="wargame_search_input"
              type="text"
              placeholder=" ..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />
          </div>

          <div className="probelm-area-container">
            <div className="filter-container-level">
              <p className="level-text">(Lv)</p>
              <button
                className={levelFilter === "all" ? "active" : ""}
                onClick={() => setLevelFilter("all")}
              >
                all
              </button>
              <button
                className={levelFilter === "" ? "active" : ""}
                onClick={() => setLevelFilter("high")}
              >
                
              </button>
              <button
                className={levelFilter === "" ? "active" : ""}
                onClick={() => setLevelFilter("intermediate")}
              >
                
              </button>
              <button
                className={levelFilter === "" ? "active" : ""}
                onClick={() => setLevelFilter("beginner")}
              >
                
              </button>
            </div>

            <div className="filter-container-category">
              <p className="category-text"></p>
              <button
                className={typeFilter === "all" ? "active" : ""}
                onClick={() => setTypeFilter("all")}
              >
                all
              </button>
              <button
                className={typeFilter === "" ? "active" : ""}
                onClick={() => setTypeFilter("system")}
              >
                
              </button>
              <button
                className={typeFilter === "" ? "active" : ""}
                onClick={() => setTypeFilter("reversing")}
              >
                
              </button>
              <button
                className={typeFilter === "" ? "active" : ""}
                onClick={() => setTypeFilter("web")}
              >
                
              </button>
              <button
                className={typeFilter === "" ? "active" : ""}
                onClick={() => setTypeFilter("crypto")}
              >
                
              </button>
              <button
                className={typeFilter === "" ? "active" : ""}
                onClick={() => setTypeFilter("forensic")}
              >
                
              </button>
              <button
                className={typeFilter === "" ? "active" : ""}
                onClick={() => setTypeFilter("misc")}
              >
                
              </button>
            </div>

            <div className="separator"></div>

            <div className="category-container">
              <p> </p>
              <p></p>
              <p></p>
            </div>

            <div className="board-container">
              {currentPosts.map((post) => (
                <div
                  key={post.id}
                  className="post"
                  onClick={() => handleClick(post.id)}
                >
                  <p>{post.quiz_title}</p>
                  <p>: {post.quiz_level}</p>
                  <p>: {post.quiz_type}</p>
                </div>
              ))}
            </div>
          </div>

          <div className="pagination-container">
            {Array.from({
              length: Math.ceil(filteredData.length / postsPerPage),
            }).map((_, index) => (
              <button
                key={index}
                className={currentPage === index + 1 ? "active" : ""}
                onClick={() => handleChangePage(index + 1)}
              >
                {index + 1}
              </button>
            ))}
          </div>
        </div>
        <div className="wargame-make-container">
          <div className="wargame-make-title">  </div>
          <div className="wargame-make-txt">
                ?
            <br></br>
                 {" "}
          </div>
          <button className="wargame-make-button"></button>
        </div>
      </div>
    </div>
  );
};

export default Wargame;
