import React, { useState, useEffect } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { useAuth } from "../contexts/AuthContext"; // useAuth  import

const Login = () => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const navigate = useNavigate();
  const { login, isLoggedIn } = useAuth(); // useAuth   login  isLoggedIn  

  useEffect(() => {
    //    isLoggedIn   
    if (isLoggedIn) {
      navigate("/"); //    
    }
  }, [isLoggedIn, navigate]); // isLoggedIn   navigate    useEffect 

  const handleLogin = async (e) => {
    e.preventDefault();

    console.log(username);
    console.log(password);
    try {
      const response = await axios.post(
        "http://ec2-3-36-34-43.ap-northeast-2.compute.amazonaws.com:8000/users/api/token/",
        {
          username: username,
          password: password,
        }
      );

      console.log(response.data);

      const token = response.data.access;
      const userId = response.data.user.user_id; //   user  

      login(token, userId); // token user     

    } catch (error) {
      console.error(" :", error);
    }
  };

  return (
    <div className="login_container">
      <div className="login_rectangle">
        <h2 className="login_title"></h2>
        <p className="welcome_text">   </p>
        <form onSubmit={handleLogin}>
          <div className="input_container">
            <label className="username_text" htmlFor="username">
              
            </label>
            <div className="login_input_container">
              <input
                className="input_id"
                type="text"
                placeholder=" "
                id="username"
                name="username"
                onChange={(e) => setUsername(e.target.value)}
                required
              />
            </div>

            <label className="pw_text" htmlFor="password">
              
            </label>
            <div className="login_input_container">
              <input
                className="input_password"
                type="password"
                placeholder=" "
                id="password"
                name="password"
                onChange={(e) => setPassword(e.target.value)}
                required
              />
            </div>
          </div>

          <div className="login_button_container">
            <button className="login_button_1" type="submit">
              
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Login;
