import React from "react";
import { useNavigate } from "react-router-dom";
import { useAuth } from "../contexts/AuthContext"; // useAuth  import
import logo from "../assets/crack_the_box_logo.png";
import final_logo from "../assets/final_logo.png";

const MyHeader = () => {
  const navigate = useNavigate();
  const { isLoggedIn, logout } = useAuth(); // useAuth   isLoggedIn  logout  

  const clickLogo = () => {
    navigate("/", { replace: true });
  };
  const clickGuild = () => {
    navigate("/guild", { replace: true });
  };
  const clickWargame = () => {
    navigate("/wargame", { replace: true });
  };
  const clickLogin = () => {
    navigate("/login", { replace: true });
  };
  const clickSignup = () => {
    navigate("/signup", { replace: true });
  };
  const handleLogout = () => {
    logout(); //   
    navigate("/", { replace: true }); //  
  };
  const clickMypage = () => {
    navigate("/Mypage", { replace: true });
  };

  return (
    <header>
      <div className="header">
        <div className="nav_left">
          <div className="nav_logo">
            <img
              alt="logo"
              className="logo"
              src={final_logo}
              onClick={clickLogo}
            ></img>
          </div>
          <div className="nav_1">
            <span className="header_text" onClick={clickWargame}>
              
            </span>
            <span className="header_text" onClick={clickGuild}>
              
            </span>
          </div>
        </div>
        <div className="nav_2">
          {isLoggedIn ? (
            <>
              <span onClick={clickMypage}></span>
              <button className="logout_btn" onClick={handleLogout}>
                
              </button>
            </>
          ) : (
            <>
              <span className="header_text" onClick={clickLogin}>
                
              </span>
              <button className="signup_button" onClick={clickSignup}>
                  
              </button>
            </>
          )}
        </div>
      </div>
    </header>
  );
};

export default MyHeader;
