import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import CTFWriterInfo from "../components/CTFWriterInfo";

const CreateWargameForm = () => {
  const [formData, setFormData] = useState({
    title: "",
    points: "",
    category: "",
    difficulty: "",
    description: "",
    hint: "",
    flag: "",
    state: false,
    file: null,
  });

  const navigate = useNavigate();

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const handleFileChange = (e) => {
    setFormData({
      ...formData,
      file: e.target.files[0],
    });
  };

  const handleDescriptionChange = (value) => {
    setFormData({
      ...formData,
      description: value,
    });
  };

  const handleCategoryChange = (category) => {
    setFormData({
      ...formData,
      category: category,
    });
  };

  const handleDifficultyChange = (difficulty) => {
    setFormData({
      ...formData,
      difficulty: difficulty,
    });
  };

  const handleStateChange = (value) => {
    setFormData({
      ...formData,
      state: value === "" ? true : false,
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const formDataToSend = {
      title: formData.title,
      points: formData.points,
      category: formData.category,
      difficulty: formData.difficulty,
      description: formData.description,
      hint: formData.hint,
      flag: formData.flag,
      state: formData.state,
    };

    try {
      // Adjust the fetch URL according to your backend endpoint
      const response = await fetch("http://ec2-3-36-34-43.ap-northeast-2.compute.amazonaws.com:8000/wargame/", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(formDataToSend),
      });

      if (!response.ok) {
        throw new Error("  .");
      }

      const responseData = await response.json();
      console.log(responseData);
      navigate("/wargame");
    } catch (error) {
      console.error("    :", error);
    }
  };

  return (
    <div className="wargame-form-container">
      <h1 className="wargame-form-title">Wargame  </h1>
      <div className="wargame_make_big_container">
        <div className="wargame-page-container">
          <form onSubmit={handleSubmit} className="wargame-form">
            <div className="form-group">
              <div className="form_title_group">
                <h3 className="form_group_title"> </h3>
                <input
                  type="text"
                  name="title"
                  value={formData.title}
                  onChange={handleChange}
                  className="form-control"
                  placeholder="  "
                />
              </div>
            </div>
            <div className="form-group">
              <div className="form_score_group">
                <h3 className="form_group_title"></h3>
                <input
                  type="text"
                  name="points"
                  value={formData.points}
                  onChange={handleChange}
                  className="form-control"
                  placeholder="  ( 500)"
                />
              </div>
            </div>
            <div className="form-group">
              <h3 className="form_group_title"> (LV)</h3>
              <div className="difficulty-buttons">
                {["", "", ""].map((level) => (
                  <button
                    type="button"
                    key={level}
                    className={`difficulty-button ${
                      formData.difficulty === level ? "selected" : ""
                    }`}
                    onClick={() => handleDifficultyChange(level)}
                  >
                    {level}
                  </button>
                ))}
              </div>
            </div>
            <div className="form-group">
              <h3 className="form_group_title">/</h3>
              <div className="category-buttons">
                {[
                  "",
                  "",
                  "",
                  "",
                  "",
                  "",
                ].map((category) => (
                  <button
                    type="button"
                    key={category}
                    className={`category-button ${
                      formData.category === category ? "selected" : ""
                    }`}
                    onClick={() => handleCategoryChange(category)}
                  >
                    {category}
                  </button>
                ))}
              </div>
            </div>
            <div className="form-group">
              <h3 className="form_group_title"> </h3>
              <ReactQuill
                value={formData.description}
                onChange={handleDescriptionChange}
                modules={CreateWargameForm.modules}
                formats={CreateWargameForm.formats}
                className="quill-editor"
                placeholder="  "
              />
            </div>
            <div className="form-group">
              <div className="form_title_group">
                <h3 className="form_group_title"> </h3>
                <input
                  type="file"
                  onChange={handleFileChange}
                  className="form-control-file"
                />
              </div>
            </div>
            <div className="form-group">
              <div className="form_title_group">
                <h3 className="form_group_title"> </h3>
                <input
                  type="text"
                  name="hint"
                  value={formData.hint}
                  onChange={handleChange}
                  className="form-control"
                  placeholder="     "
                />
              </div>
            </div>
            <div className="form-group">
              <div className="form_title_group">
                <h3 className="form_group_title"></h3>
                <input
                  type="text"
                  name="flag"
                  value={formData.flag}
                  onChange={handleChange}
                  className="form-control"
                  placeholder="   "
                />
              </div>
            </div>
          </form>
        </div>
        <div className="wargame_right_container">
          <CTFWriterInfo />
          <div className="create_wargame_caution">
            <h3>   </h3>
            <div className="create_wargame_caution_txt">
                     
                
            </div>
            <button
              className="create_wargame_submit_1"
              type="submit"
              onClick={handleSubmit}
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

// Quill modules and formats for the toolbar
CreateWargameForm.modules = {
  toolbar: [
    [{ header: "1" }, { header: "2" }, { font: [] }],
    [{ list: "ordered" }, { list: "bullet" }],
    ["bold", "italic", "underline", "strike", "blockquote"],
    ["link", "image"],
    [{ align: [] }],
    [{ color: [] }, { background: [] }],
    ["clean"],
  ],
};

CreateWargameForm.formats = [
  "header",
  "font",
  "list",
  "bullet",
  "bold",
  "italic",
  "underline",
  "strike",
  "blockquote",
  "link",
  "image",
  "align",
  "color",
  "background",
];

export default CreateWargameForm;
