import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useAuth } from "../contexts/AuthContext";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import CTFWriterInfo from "../components/CTFWriterInfo";

const CreateCTFForm = () => {
  const [formData, setFormData] = useState({
    title: "",
    points: "",
    category: "",
    difficulty: "",
    description: "",
    hint: "",
    flag: "",
    state: false,
    file: null,
  });

  const navigate = useNavigate();

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const handleFileChange = (e) => {
    setFormData({
      ...formData,
      file: e.target.files[0],
    });
  };

  const handleDescriptionChange = (value) => {
    setFormData({
      ...formData,
      description: value,
    });
  };

  const handleCategoryChange = (category) => {
    setFormData({
      ...formData,
      category: category,
    });
  };

  const handleDifficultyChange = (difficulty) => {
    setFormData({
      ...formData,
      difficulty: difficulty,
    });
  };

  const handleStateChange = (value) => {
    setFormData({
      ...formData,
      state: value === "" ? true : false,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // Submit formData to the DB
    console.log(formData);
    navigate("/adminctfcreatelist"); //   
  };

  return (
    <div className="ctf-form-container">
      <h1 className="ctf-form-title">CTF  </h1>
      <div className="ctf-page-container">
        <form onSubmit={handleSubmit}>
          <div className="form-group">
            <div className="form_title_group">
              <h3 className="form_group_title"> </h3>
              <input
                type="text"
                name="title"
                value={formData.title}
                onChange={handleChange}
                placeholder="  "
              />
            </div>
          </div>
          <div className="form-group">
            <div className="form_score_group">
              <h3 className="form_group_title"></h3>
              <input
                type="text"
                name="points"
                value={formData.points}
                onChange={handleChange}
                placeholder="  ( 500)"
              />
            </div>
          </div>
          <div className="form-group">
            <h3 className="form_group_title"> (LV)</h3>
            <div className="difficulty-buttons">
              {["", "", ""].map((level) => (
                <button
                  type="button"
                  key={level}
                  className={`difficulty-button ${
                    formData.difficulty === level ? "selected" : ""
                  }`}
                  onClick={() => handleDifficultyChange(level)}
                >
                  {level}
                </button>
              ))}
            </div>
          </div>
          <div className="form-group">
            <h3 className="form_group_title">/</h3>
            <div className="category-buttons">
              {[
                "",
                "",
                "",
                "",
                "",
                "",
              ].map((category) => (
                <button
                  type="button"
                  key={category}
                  className={`category-button ${
                    formData.category === category ? "selected" : ""
                  }`}
                  onClick={() => handleCategoryChange(category)}
                >
                  {category}
                </button>
              ))}
            </div>
          </div>
          <div className="form-group">
            <h3 className="form_group_title"> </h3>
            <ReactQuill
              value={formData.description}
              onChange={handleDescriptionChange}
              modules={CreateCTFForm.modules}
              formats={CreateCTFForm.formats}
              placeholder="  "
            />
          </div>
          <div className="form-group">
            <div className="form_title_group">
              <h3 className="form_group_title"> </h3>
              <input type="file" onChange={handleFileChange} />
            </div>
          </div>
          <div className="form-group">
            <div className="form_title_group">
              <h3 className="form_group_title"> </h3>
              <input
                type="text"
                name="hint"
                value={formData.hint}
                onChange={handleChange}
                placeholder="     "
              />
            </div>
          </div>
          <div className="form-group">
            <div className="form_title_group">
              <h3 h3 className="form_group_title">
                
              </h3>
              <input
                type="text"
                name="flag"
                value={formData.flag}
                onChange={handleChange}
                placeholder="   "
              />
            </div>
          </div>
        </form>
        <div className="create_ctf_other_info_container">
          <CTFWriterInfo />
          <div>
            <div className="status-form-group">
              <h3 className="form_group_title">  </h3>
              <div className="status-form-txt">
                 CTF    
              </div>
              <div className="status-form-select">
                <div className="status-form-select-txt"> </div>
                <div className="status-form-select-option">
                  <select
                    value={formData.state ? "" : ""}
                    onChange={(e) => handleStateChange(e.target.value)}
                  >
                    <option value=""></option>
                    <option value=""></option>
                  </select>
                </div>
              </div>
            </div>
          </div>
          <div className="create_ctf_caution">
            <h3>CTF   </h3>
            <div className="create_ctf_caution_txt">
                     
                
            </div>
            <button
              className="create_ctf_submit"
              type="submit"
              onClick={handleSubmit}
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

// Quill modules and formats for the toolbar
CreateCTFForm.modules = {
  toolbar: [
    [{ header: "1" }, { header: "2" }, { font: [] }],
    [{ list: "ordered" }, { list: "bullet" }],
    ["bold", "italic", "underline", "strike", "blockquote"],
    ["link", "image"],
    [{ align: [] }],
    [{ color: [] }, { background: [] }],
    ["clean"],
  ],
};

CreateCTFForm.formats = [
  "header",
  "font",
  "list",
  "bullet",
  "bold",
  "italic",
  "underline",
  "strike",
  "blockquote",
  "link",
  "image",
  "align",
  "color",
  "background",
];

export default CreateCTFForm;
